@import url("https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap");

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Barlow", sans-serif;
  color: #fff;
}

body {
  background: #252836;
  color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #fff;
}

a {
  text-decoration: none;
  color: inherit;
}

input,
button,
textarea {
  outline: none;
  border: none;
  font-family: inherit;
}
button,
a {
  border: none;
  outline: none;
  color: inherit;
  font-family: inherit;
}
ul {
  list-style: none;
}

::-webkit-scrollbar {
  width: 0;
}